.message-input-container{
  outline:none;
  background-color: rgb(92, 80, 80);
  border-radius: 10px;
  font-size: larger;
  padding-top:10px;
  padding-bottom: 10px;
  padding-left: 5px;
  max-height: 98px;
  overflow-y: auto;
  user-select: text;
  width: 40px;
  -webkit-user-select: text;
  -moz-user-select: text;
  white-space: pre-wrap;
  overflow-x: hidden;
  overflow-wrap: break-word;
  display: inline-block;
}

.message-input-wrapper{
  width: 190px;
}

.container-send-message-input{
  display: flex;
  align-items: center;
}

.message-input-wrapper > .img-message-input,
.message-input-wrapper > .message-input-container{
  padding-left:10px;
}

.message-input-container:empty:before {
  content: attr(data-placeholder);
  color: rgba(255, 255, 255, 0.404);
  cursor: text;
}

.message-input-container::-webkit-scrollbar{
  width: 5px;
  border-radius: 10px;
  background-color: #1c2a36;
}

.message-input-container::-webkit-scrollbar-thumb{
  border-radius: 10px;
  background-color: rgb(63, 77, 116);
}


.img-message-input{
  margin-top: 10px;
  background-color: rgb(92, 80, 80);
  box-shadow: 0 10px 1px 0px rgb(92, 80, 80);
  white-space: nowrap;
  overflow:hidden;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  overflow-x: auto;
  max-width: 142.5px;
}

.img-message-input::-webkit-scrollbar{
  background-color: rgb(8, 8, 8);
  height: 4px;
}

.img-message-input::-webkit-scrollbar-thumb{
  background-color: rgb(255, 255, 255);
}

.img-detail-wrapper{
  position: relative;
  display: inline-block;
  margin-top:10px;
  margin-left:10px;
}

.img-detail-wrapper img{
  object-fit: cover;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  object-position: center;
}

.delete-symbol-message{
  opacity: 0;
  transition: 0.3s ease-in-out;
  color:white;
  position: absolute;
  right: 0;
  font-size: 1rem;
  top: 0;
  cursor: pointer;
  padding:0.3em;
  z-index: 4;
}

.img-detail-wrapper:hover .delete-symbol-message{
  opacity: 1;
}

.img-detail-wrapper::before{
  content: "";
  opacity: 0;
  position: absolute;
  top:0;
  padding:0;
  left: 0;
  bottom:0;
  right: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.img-detail-wrapper:hover::before{
  opacity: 1;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.568);
  z-index: 3;
}